      <path fill="#f6f6f6" d="M545.962 183.777c-53.796 196.576-111.592 361.156-163.49 490.74 11.7 44.494 22.8 89.49 33.1 134.883h404.07c-71.294-258.468-185.586-483.84-273.68-625.623z"></path>
      <path fill="#f7f7f7" d="M153.89 0c74.094 180.678 161.088 417.448 228.483 674.517C449.67 506.337 527.063 279.465 592.56 0H153.89z"></path>
      <path fill="#fbfbfc" d="M153.89 0H0v809.5h415.57C345.477 500.938 240.884 211.874 153.89 0z"></path>
      <path fill="#ebebec" d="M1144.22 501.538c52.596-134.583 101.492-290.964 134.09-463.343 1.2-6.1 2.3-12.298 3.4-18.497 0-.2.1-.4.1-.6 1.1-6.3 2.3-12.7 3.4-19.098H902.536c105.293 169.28 183.688 343.158 241.684 501.638v-.1z"></path>
      <path fill="#e1e1e1" d="M1285.31 0c-2.2 12.798-4.5 25.597-6.9 38.195C1321.507 86.39 1379.603 158.98 1440 257.168V0h-154.69z"></path>
      <path fill="#e7e7e7" d="M1278.31,38.196C1245.81,209.874 1197.22,365.556 1144.82,499.838L1144.82,503.638C1185.82,615.924 1216.41,720.211 1239.11,809.6L1439.7,810L1439.7,256.768C1379.4,158.78 1321.41,86.288 1278.31,38.195L1278.31,38.196z"></path>
    </svg>
    <p v-show="page !== 2" id="name">普信天宇</p>
    <img v-show="page !== 2" class="close2" src="resource/assets/close-grey.png" alt="close" @click="back">
    <div id="signBox">
      <img v-show="page !== 2" class="close" src="resource/assets/close-grey.png" alt="close" @click="back">
      <div v-show="page === 0" id="sign-start">
        <!--<img class="icon" src="resource/assets/logo.png" alt="logo">-->
        <p class="cname">普信天宇</p>
        <p id="sign-in">登录</p>
        <p id="sign-prompt">进入你的中心</p>
        <p class="sign-placeholder" :class="{'active':inputStatus, 'active2':inputData, 'active3': error}">邮箱 / 手机号</p>
        <input class="account" :class="{'active': error2}" type="text" @focus="input" @focusout="leave" @keyup="type" @keyup.enter="next">
        <div class="input-bottom">
          <div class="origin"></div>
          <transition name="input">
            <div v-show="inputStatus" class="new"></div>
          </transition>
          <div v-show="error" class="error"></div>
        </div>
        <p id="forget">忘记密码</p>
        <p id="options">更多选项</p>
        <div class="next" @click="next">下一步</div>
        <div id="user"></div>
      </div>
      <div v-show="page === 1" id="in-or-up">
        <p class="cname">普信天宇</p>
        <p id="welcome">欢迎</p>
        <p id="up-prompt">{{prompt}}</p>
        <p class="sign-placeholder" :class="{'active':inputStatus, 'active2':inputData, 'active3': error}">请输入密码</p>
        <input class="password" :class="{'active': error2}" type="password" @focus="input" @focusout="leave" @keyup="type" @keyup.enter="next">
        <div class="input-bottom">
          <div class="origin"></div>
          <transition name="input">
            <div v-show="inputStatus" class="new"></div>
          </transition>
          <div v-show="error" class="error"></div>
        </div>
        <div id="back" class="next" @click="previous">上一步</div>
        <div class="next" @click="next">{{button}}</div>
        <div class="back" @click="previous"></div>
        <div id="user2"></div>
      </div>
      <img v-show="page === 2" id="success-img" src="resource/assets/success.png" alt="success">
      <transition name="text1">
        <p v-show="page === 2" class="success-text">{{resultRender}}成功</p>
      </transition>
    </div>
    <transition name="check">
      <div v-show="check" class="checkBox">
        <form action="/form.php" method="get">
          <p>验证码</p>
          <img id="img" src="/checkcode.php?" @click="change">
          <input id="check" name="code" @focus="input3" @focusout="leave3" @keyup.enter="submit">
          <div class="input-bottom">
            <div class="origin"></div>
            <transition name="input">
              <div v-show="inputStatus3" class="new"></div>
            </transition>
            <div v-show="error3" class="error"></div>
          </div>
          <input id="check-submit" type="submit" value="提交" @click.prevent="submit">
        </form>
      </div>
    </transition>
    <div v-show="check" class="grey-cover" @click="cancelCheck"></div>
  </div>
