<script>
import {checkCookie} from '../../resource/js/cache'
import $ from 'jquery'
export default {
  name: 'sign',
  data () {
    return {
      inputStatus: false,
      inputStatus3: false,
      inputData: false,
      error: false,
      error2: false,
      error3: false,
      page: 0,
      prompt: '',
      button: '登录',
      check: false,
      signMode: '',
      account: '',
      password: '',
      mode: -1,
      lock: false
    }
  },
  methods: {
    input: function () {
      this.inputStatus = true
      this.lock = false
    },
    input3: function () {
      this.inputStatus3 = true
    },
    leave: function () {
      var self = this
      setTimeout(function () {
        if (!self.lock) {
          self.inputStatus = false
        }
      }, 30)
      if (event.target.value === '') {
        this.inputData = false
      }
    },
    leave3: function () {
      this.inputStatus3 = false
    },
    type: function () {
      if (event.target.value !== '') {
        this.inputData = true
      } else {
        this.inputData = false
      }
    },
    next: function () {
      var self = this
      if ($('.account').val() !== '' || $('.password').val() !== '') {
        this.error = false
        if (!this.$root.adapt) {
          this.lock = true
          this.error2 = false
        }
        if (this.page === 0) {
          $.ajax({
            async: false,
            url: '/login_first/',
            type: 'GET',
            // eslint-disable-next-line no-undef
            data: {account: $('.account').val(), step1: 'step1', csrfmiddlewaretoken: csrf_token},
            success: function (data) {
              self.signMode = data
              if (data === 'sign in') {
                self.button = '登录'
                self.mode = 1
              } else {
                self.button = '注册'
                self.mode = 0
              }
              self.account = $('.account').val()
              self.page++
              self.prompt = '输入密码加入我们'
              if (!self.$root.adapt) {
                $('.password').attr('placeholder', '请输入密码')
              }
              setTimeout(function () {
                $('.password').focus()
              }, 100)
            }
          })
        } else if (this.page === 1) {
          this.password = $('.password').val()
          if (this.mode === 1) {
            $.ajax({
              url: '/sign_in/',
              type: 'GET',
              // eslint-disable-next-line no-undef
              data: {account: self.account, psd: self.password, csrfmiddlewaretoken: csrf_token},
              success: function (data) {
                self.error3 = false
                self.inputStatus3 = true
                self.check = false
                self.page = 2
                self.lock = false
                // checkCookie(1, 'DEAN')
                // self.$root.eventHub.$emit('sign')
                // setTimeout(function () {
                //   self.$router.push({path: '/home'})
                // }, 1800)
                checkCookie(1, self.account)
                self.$root.eventHub.$emit('sign')
                setTimeout(function () {
                  self.$router.push({path: '/home'})
                }, 1800)
                $('#signBox input').val('')
              }
            })
          } else if (this.mode === 0) {
            $.ajax({
              url: '/sign_up/',
              type: 'GET',
              // eslint-disable-next-line no-undef
              data: {account: self.account, psd: self.password, csrfmiddlewaretoken: csrf_token},
              success: function (data) {
                self.error3 = false
                self.inputStatus3 = true
                self.check = false
                self.page = 2
                self.lock = false
                // checkCookie(1, 'DEAN')
                // self.$root.eventHub.$emit('sign')
                // setTimeout(function () {
                //   self.$router.push({path: '/home'})
                // }, 1800)
                checkCookie(1, self.account)
                self.$root.eventHub.$emit('sign')
                setTimeout(function () {
                  self.$router.push({path: '/home'})
                }, 1800)
                $('#signBox input').val('')
              }
            })
          }
          // this.check = true
          // this.$root.eventHub.$emit('prompt')
          // setTimeout(function () {
          //   $('#check').focus()
          // }, 300)
        }
      } else {
        this.error = true
        if (window.screen.width < 768) {
          this.error2 = true
        }
      }
    },
    previous: function () {
      this.error = false
      $('.password').val('')
      if (this.$root.adapt) {
        this.inputStatus = false
      } else {
        this.lock = true
      }
      this.inputData = true
      this.page--
      setTimeout(function () {
        $('.account').focus()
      }, 100)
    },
    back: function () {
      this.$router.push({path: '/home'})
    },
    submit: function () {
      var returnData = ''
      var temp = $('#check').val()
      var self = this
      $.ajax({
        async: false, // 异步处理，使ajax先执行
        url: '/form.php',
        type: 'GET',
        data: {code: temp},
        success: function (data) {
          if (data === '正确') {
            $.ajax({
              url: '/sign_in_up.php',
              type: 'GET',
              data: {account: self.account, psd: self.password, step2: self.signMode},
              success: function (data) {
                self.error3 = false
                self.inputStatus3 = true
                self.check = false
                self.page = 2
                // checkCookie(1, self.account)
                // self.$root.eventHub.$emit('sign')
                // setTimeout(function () {
                //   self.$router.push({path: '/home'})
                // }, 1800)
                // $('#signBox input').val('')
              }
            })
          } else {
            self.error3 = true
          }
        }
      })
      return returnData
    },
    change: function () {
      $('#img').attr('src', '/checkcode.php?r=' + Math.random())
    },
    cancelCheck: function () {
      this.check = false
      this.error3 = false
      this.inputStatus3 = false
      if (this.$root.adapt) {
        setTimeout(function () {
          $('.password').focus()
        }, 100)
      }
    }
  },
  computed: {
    resultRender: function () {
      if (this.mode === 1) {
        return '登录'
      } else {
        return '注册'
      }
    },
    mobile: function () {
      if (window.screen.width < 768 && this.inputStatus) {
        return false
      } else {
        return true
      }
    }
  },
  mounted () {
    if (!this.$root.adapt) {
      $('.account').attr('placeholder', '邮箱 / 手机号')
      this.$root.eventHub.$emit('nav', 3)
    }
    $('.account').focus()
    var self = this
    self.$root.eventHub.$emit('loading')
    setTimeout(function () {
      self.loaded = true
      self.$root.eventHub.$emit('load')
    }, 1000)
  }
}
</script>

