      color: #333333;
    }
    .sign-placeholder{
      font-size: 14px;
      position: absolute;
      padding-left: 3px;
      color: #808080;
      z-index: 2;
      -webkit-transition: all .3s;
      -moz-transition: all .3s;
      -o-transition: all .3s;
      transition: all .3s;
    }
    #sign-start .sign-placeholder{
      top: 188px;
    }
    #in-or-up .sign-placeholder{
      top: 196px;
    }
    #sign-start .sign-placeholder.active{
      font-size: 6px;
      color: #1296DB;
      top: 164px
    }
    #sign-start .sign-placeholder.active2{
      font-size: 6px;
      top: 164px;
    }
    .sign-placeholder.active3{
      color: #DC143C!important;
    }
    #in-or-up .sign-placeholder.active{
      font-size: 6px;
      color: #1296DB;
      top: 172px;
    }
    #in-or-up .sign-placeholder.active2{
      font-size: 6px;
      top: 172px;
    }
    .account, .password{
      width: 100%;
      height: 30px;
      font-size: 14px;
      color: #333333;
      position: relative;
      z-index: 3;
    }
    .account{
      margin: 48px 2px 0;
    }
    .password{
      margin: 38px 2px 0;
    }
    .input-bottom{
      width: calc(100% - 2px);
      height: 6px;
      left: 2px;
    }
    #in-or-up .password{
      margin: 50px 2px 0;
    }
    #forget{
      color: #1296DB;
      font-size: 14px;
      line-height: 24px;
      margin: 22px 3px;
      cursor: pointer;
      float: left;
    }
    #options{
      color: #1296DB;
      font-size: 14px;
      margin: 24px 3px;
      cursor: pointer;
      float: left;
      clear: left;
    }
    @-webkit-keyframes enter {
      0%{
        -webkit-transform: translateX(100%);
      }
      100%{
        -webkit-transform: translateX(0);
      }
    }
    @-moz-keyframes enter {
      0%{
        -moz-transform: translateX(100%);
      }
      100%{
        -moz-transform: translateX(0);
      }
    }
    @-o-keyframes enter {
      0%{
        -o-transform: translateX(100%);
      }
      100%{
        -o-transform: translateX(0);
      }
    }
    @keyframes enter {
      0%{
        transform: translateX(100%);
      }
      100%{
        transform: translateX(0);
      }
    }
    @-webkit-keyframes leave {
      0%{
        -webkit-transform: translateX(0);
      }
      100%{
        -webkit-transform: translateX(-100%);
      }
    }
    @-moz-keyframes leave {
      0%{
        -moz-transform: translateX(0);
      }
      100%{
        -moz-transform: translateX(-100%);
      }
    }
    @-o-keyframes leave {
      0%{
        -o-transform: translateX(0);
      }
      100%{
        -o-transform: translateX(-100%);
      }
    }
    @keyframes leave {
      0%{
        transform: translateX(0);
      }
      100%{
        transform: translateX(-100%);
      }
    }
    .next{
      width: 88px;
      height: 36px;
      border-radius: 3px;
      background: #1296DB;
      box-shadow: 0 1px 2px grey;
      margin: 15px 0;
      float: right;
      text-align: center;
      font-size: 14px;
      font-weight: bold;
      line-height: 36px;
      color: white;
      cursor: pointer;
      transition: all .2s;
    }
    #in-or-up .next{
      margin: 50px 0;
    }
    #in-or-up #back{
      float: left;
      margin: 50px 4px;
    }
    .next:active{
      box-shadow: 0 3px 12px grey;
      background: #1296DB;
    }
    #welcome{
      font-size: 24px;
      padding: 23px 3px 0;
    }
    #up-prompt{
      font-size: 14px;
      height: 24px;
      padding: 8px 3px 0;
    }
    #success-img{
      width: 90px;
      margin: 100px calc((100% - 90px) / 2) 16px;
    }
    .success-text{
      font-size: 24px;
    }
    .checkBox{
      width: 210px;
      height: 228px;
      padding: 15px;
      background: white;
      position: fixed;
      top: calc((100% - 278px) / 2);
      left: calc((100% - 240px) / 2);
      z-index: 50;
    }
    .checkBox p{
      font-size: 18px;
    }
    .checkBox img{
      width: 210px;
      height: 60px;
      margin: 20px 0 0;
    }
    .checkBox input:first-of-type{
      width: 210px;
      height: 40px;
      border: none;
      outline: none;
      border-bottom: 1px solid #d9d9d9;
      margin-top: 12px;
      font-size: 14px;
      line-height: 40px;
    }
    .checkBox input:last-of-type{
      width: 210px;
      height: 40px;
      border: none;
      outline: none;
      margin-top: 30px;
      border-radius: 3px;
      background: #1296DB;
      box-shadow: 0 1px 2px grey;
      color: white;
      font-size: 14px;
      font-weight: bold;
      cursor: pointer;
    }
  }

  @media (max-width: 599px) and (min-device-width: 768px) {
    #sign{
      overflow-y: scroll;
    }
    #sign svg{
      display: none;
    }
    #signBox{
      width: calc(100% - 80px);
      height: calc(100% - 72px);
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  @media (min-width: 600px) and (min-device-width: 768px) {
    #signBox{
      width: 370px;
      height: 428px;
      position: absolute;
      top: calc((100% - 500px) / 2);
      left: calc((100% - 450px) / 2);
      box-shadow: 0 1px 2px #A9A9A9;
    }
  }

  @media (min-width: 600px) and (max-height: 600px) and (min-device-width: 768px) {
    html{
      height: 100%;
    }
    #sign{
      overflow-y: scroll;
    }
    #sign svg{
      position: fixed;
    }
    #signBox{
      top: 34px;
    }
  }
</style>



